
import { getCartLists, getAddress, getWxSettings, getShippingFee, getCoupons, changeAddress } from '@/api/index.js';
import Picker from 'vux/src/components/picker/index.vue'

import footerSubmit from '@/components/footerSubmit/footerSubmit.vue';
export default {
name: 'Settlement',
props:{
shopid:{
require: true
}
},
data() {
return {
coordinateIcon: require('../../assets/takeout06.png'),
arrowIcon: require('../../assets/return.png'),
totalMoney: 0,
carlist: null,
address:null,

addressStr:'',
remark:'',
orderData:null,

showSelectCoupon:false,
selectedCoupon:[],
coupons: [],
};
},
computed:{
orderTotalMoney(){
if(!this.carlist) return '';
if(!this.orderData) return '';
return (this.carlist.total_price * 1 + this.orderData.shipping_fee * 1).toFixed(2);
}
},
methods: {
selectCouponChange(){

},
handleChooseAddress(){
console.log(this.wx);
this.wx.openAddress({
success: res => {
changeAddress({
user_phone: res.telNumber || '',
user_name: res.userName || '',
user_address: res.detailInfo || '',
user_province: res.provinceName || '',
user_city: res.cityName || '',
user_country: res.countryName || '',
zip_code: res.postalCode || '',
user_id: this.$store.state.user.userid
}).then(res=>{
if(res.data.code == 1){
this.address.aid = res.data.data.address_id;
this.address.user_name = res.userName;
this.address.user_address = res.detailInfo;
this.address.user_phone = res.telNumber;
this.getShippingFee();
}else{
this.feedback.Toast({
msg:res.data.info,
timeout: 1200
})
}
});
this.addressStr = JSON.stringify(res);
}
});
},
handleGoPayment() {
this.$router.push({
name: 'Payment',
query: {
totalMoney: this.totalMoney
}
});
},
handleSubmit(){
console.log(this.selectedCoupon);
},

getCartLists() {
getCartLists({
is_waimai: 1,
shop_id: this.shopid,
user_id: this.$store.state.user.userid
}).then(res => {
if (res.data.code == 1) {
this.carlist = res.data.data;
if(this.address) this.getShippingFee();
this.getCoupons();
console.log(this.carlist);
}
})
},


getCoupons(){
getCoupons({
shop_id:this.shopid,
user_id:this.$store.state.user.userid,
total_price:this.carlist.total_price,
discounts_price:this.carlist.discounts_price
}).then(res=>{
if(res.data.code == 1){
this.coupons = res.data.data;
}
console.log('优惠券',res)
})
},


getShippingFee(){
getShippingFee({
shop_id:this.shopid,
address_id:this.address.aid,
order_no: '',
order_price:this.carlist.total_price,
remark:this.remark || '无'
}).then(res=>{
if(res.data.code == 1){
this.orderData = res.data.data;
}else{
this.feedback.Toast({
msg:res.data.info,
timeout: 1200
})
}
console.log('运费',res);
})
}
},
mounted() {
console.log(this.wx);
this.getCartLists();
getAddress({ uid: this.$store.state.user.userid }).then(res=>{
if(res.data.code == 1){
this.address = res.data.data[0];
console.log(this.address);
if(this.carlist) this.getShippingFee();
}else{
this.feedback.Toast({
msg:res.data.info
});
}
});

getWxSettings().then(res => {
let data = res.data.data;
this.wx.config({
debug: global.isDev,
appId: data.appid,
timestamp: data.timestamp,
nonceStr: data.nonceStr,
signature: data.signature,
jsApiList: ['openAddress']
});
});
},
components:{
footerSubmit,
Picker
}
};




